.ant-table-header, .ant-table-body-inner {
  overflow-y: scroll;
}
.ant-table-fixed-left .ant-table-header,
.ant-table-fixed-right .ant-table-header {
  margin-bottom: auto !important;
  padding-bottom: auto !important;
}
.ant-table-header {
  margin-bottom: 0 !important;
  padding-bottom: 0 !important;
  overflow-x: hidden !important;
}
.ant-table-body {
  overflow-x: auto !important;
}
.ant-table-header::-webkit-scrollbar,
.ant-table-body-inner::-webkit-scrollbar,
.ant-table-body::-webkit-scrollbar {
  background: white;
}
.ant-table-body::-webkit-scrollbar-thumb,
.ant-table-body-inner::-webkit-scrollbar-thumb {
  background: #eee;
  border: 3px solid white;
  border-radius: 10px;
}
.carousel_to {
  position: absolute;
  bottom: 10px;
  font-size: 30px;
  color: white;
  z-index: 999;
  cursor: pointer;
  opacity: 0.5;
  transition: 0.3s;
}

.carousel_to:hover {
  opacity: 1;
}

.carousel_to.left {
  left: 10px;
}
.carousel_to.right {
  right: 10px;
}
.components-table-demo-nested .ant-table-expanded-row > td:last-child {
